* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #F5F7FB;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
}

.topBlob {
  position: absolute;
  top: 0;
  right: 0;
}

.bottomBlob {
  position: absolute;
  bottom: 0;
  left: 0;
}

.start--title {
  font-family: 'Karla', sans-serif;
  color: #293264;
  font-size: 32px;
  line-height: 36.5px;
  text-align: center;
}

.start--button {
  background: #4D5B9E;
  color: #F5F7FB;
  font-family: 'Inter', sans-serif;
  font-size: 16px;
  line-height: 19.5px;
  width: 193px;
  height: 52px;
  margin-top: 55px;
  border-radius: 15px;
  border: none;
}

.quiz {
  display: flex;
  flex-direction: column;
}

.List {
  list-style: none;
  padding: 0;
}

.question {
  border-bottom: .8px solid #dbdef0;
  margin: auto;
  width: 90%;
  text-align: center;
}

.question--title {
  color: #293264;
  font-family: Karla,sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
}

.question--answers {
  display: flex;
  flex-direction: row;
  list-style: none;
  margin: 4px auto 8px;
  justify-content: center;
  align-items: center;
}

.question--answers-choice {
  background: #F5F7FB;
  border-radius: 8px;
  border: 1px solid #4D5B9E;
  font-family: 'Inter', sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  padding: 3px 10px;
  color: #293264;
  margin: 0 5px;
}

.question--answers-choice.active {
  background: #D6DBF5;
  border: 1px solid #D6DBF5;
}

.quiz--results {
  display: initial;
  visibility: hidden;
  height: 20px;
  margin-top: 5px;
  color: #293264;
  font-family: 'Inter', sans-serif;
  font-size: 12px;
  font-weight: 400;
  text-align: center;
}

.quiz--results.active {
  visibility: visible;
}

.quiz--button-wrapper {
  display: flex;
  justify-content: center;
  margin: 10px;
}

.quiz--button {
  border-radius: 8px;
  border: 1px solid #4D5B9E;
  font-family: 'Inter', sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  padding: 3px 10px;
  margin: 0 5px;
}

.quiz--button:hover {
  opacity: 0.8;
  cursor: pointer;
}

.quiz--button.submit {
  color: #F5F7FB;
  background: #293264;
}

.quiz--button.reset {
  background: #F5F7FB;
  color: #293264;
}